.places {
  $this: &;
  // &:hover
  //   #{$this}__in
  //     animation: border .4s ease 1 forwards
  &__in {
    position: relative;
    border-radius: 50%;
    margin: 0 auto 40px;
    box-sizing: border-box;
    // border: 10px solid #fff
    &:before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 1;
      border-radius: 50%; }
    > span {
      @include vcenter; } }

  svg {
    position: absolute;
    top: 0;
    right: 0;
    width: 140px;
    height: 140px;
    z-index: 9;
    transform: rotateY(-180deg) rotateZ(-90deg);
    circle {
      stroke-dasharray: 790px;
      stroke-dashoffset: -440px;
      stroke-width: 10px;
      stroke: white;
      fill: none;
      animation: countdown 5s linear infinite forwards; } }


  &_white {
    color: #fff;
    #{$this}__title {
      font-size: 20px;
      margin-bottom: 20px;
      @include ubuntu-l; }
    #{$this}__in {
      width: 140px;
      height: 140px;
      &:before {
        border: 10px solid $dark-orange; } } } }

@keyframes countdown {
  from {
    stroke-dashoffset: -440px; }
  to {
    stroke-dashoffset: 0px; } }


@keyframes border {
  0% {
    box-shadow: 60px -60px 0 2px $dark-orange, -60px -60px 0 2px $dark-orange, -60px 60px 0 2px $dark-orange, 60px 60px 0 2px $dark-orange, 0 0 0 2px $dark-orange; }
  25% {
    box-shadow: 0 -125px 0 2px $dark-orange, -60px -60px 0 2px $dark-orange, -60px 60px 0 2px $dark-orange, 60px 60px 0 2px $dark-orange, 0 0 0 2px #fff; }
  50% {
    box-shadow: 0 -125px 0 2px $dark-orange, -125px 0px 0 2px $dark-orange, -60px 60px 0 2px $dark-orange, 60px 60px 0 2px $dark-orange, 0 0 0 2px #fff; }
  75% {
    box-shadow: 0 -125px 0 2px $dark-orange, -125px 0px 0 2px $dark-orange, 0px 125px 0 2px $dark-orange, 60px 60px 0 2px $dark-orange, 0 0 0 2px #fff; }
  100% {
    box-shadow: 0 -125px 0 2px $dark-orange, -125px 0px 0 2px $dark-orange, 0px 125px 0 2px $dark-orange, 120px 40px 0 2px $dark-orange, 0 0 0 2px #fff; } }
