.slider {
  position: relative;
  height: 100%;
  min-height: 100%;
}

.slider .slick-dots {
  position: absolute;
  bottom: 80px;
  left: 50%;
  transform: translateX(-50%);
  margin: 0 -3px;
}

.slider .slick-dots li {
  display: inline-block;
  vertical-align: middle;
  margin: 0 3px;
}

.slider .slick-dots li.slick-active > button {
  border-color: transparent;
  background: #6ecdbe;
}

.slider .slick-dots button {
  display: block;
  font-size: 0;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 2px solid #fff;
}

.slick-list,
.slick-track {
  height: 100%;
}

.style-list {
  margin: 40px 0;
  font-size: 0;
}

.style-list__item {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  font-size: 16px;
  font-family: "Ubuntu", Arial, Helvetica, serif;
