}

.places__in {
  position: relative;
  border-radius: 50%;
  margin: 0 auto 40px;
  box-sizing: border-box;
}

.places__in:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  border-radius: 50%;
}

.places__in > span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.places svg {
  position: absolute;
  top: 0;
  right: 0;
  width: 140px;
  height: 140px;
  z-index: 9;
  transform: rotateY(-180deg) rotateZ(-90deg);
}

.places svg circle {
  stroke-dasharray: 790px;
  stroke-dashoffset: -440px;
  stroke-width: 10px;
  stroke: white;
  fill: none;
  animation: countdown 5s linear infinite forwards;
}

.places_white {
  color: #fff;
}

.places_white .places__title {
  font-size: 20px;
  margin-bottom: 20px;
  font-family: "Ubuntu", Arial, Helvetica, serif;
  font-weight: 300;
}

.places_white .places__in {
  width: 140px;
  height: 140px;
}

.places_white .places__in:before {
  border: 10px solid #e46b40;
}

@keyframes countdown {
  from {
    stroke-dashoffset: -440px;
  }
  to {
    stroke-dashoffset: 0px;
  }
}

@keyframes border {
  0% {
    box-shadow: 60px -60px 0 2px #e46b40, -60px -60px 0 2px #e46b40, -60px 60px 0 2px #e46b40, 60px 60px 0 2px #e46b40, 0 0 0 2px #e46b40;
  }
  25% {
    box-shadow: 0 -125px 0 2px #e46b40, -60px -60px 0 2px #e46b40, -60px 60px 0 2px #e46b40, 60px 60px 0 2px #e46b40, 0 0 0 2px #fff;
  }
  50% {
    box-shadow: 0 -125px 0 2px #e46b40, -125px 0px 0 2px #e46b40, -60px 60px 0 2px #e46b40, 60px 60px 0 2px #e46b40, 0 0 0 2px #fff;
  }
  75% {
    box-shadow: 0 -125px 0 2px #e46b40, -125px 0px 0 2px #e46b40, 0px 125px 0 2px #e46b40, 60px 60px 0 2px #e46b40, 0 0 0 2px #fff;
  }
  100% {
    box-shadow: 0 -125px 0 2px #e46b40, -125px 0px 0 2px #e46b40, 0px 125px 0 2px #e46b40, 120px 40px 0 2px #e46b40, 0 0 0 2px #fff;
  }
}
